section.dark{background-color:var(--dark2)}section.light{background-color:var(--surface)}.container{width:100%;max-width:1200px;margin:0 auto;padding:0 1rem;text-align:center;padding:4rem 2rem}.title{font-size:48px;font-weight:700;line-height:56px;color:var(--on-surface)}.title.dark{color:var(--secondary2)}.title.light{color:var(--on-surface)}.subtitle{font-size:20px;font-weight:400;line-height:28px;margin-bottom:3rem}.subtitle.light{color:var(--secondary)}.subtitle.dark{color:var(--secondary2)}.steps{display:flex;justify-content:space-between;align-items:center;position:relative;flex-wrap:wrap;gap:2rem}.step{max-width:300px;height:auto;flex:1}.icon{width:96px;height:96px;display:block;margin:0 auto;margin-bottom:1rem}.stepNumber{font-size:12px;font-weight:700;line-height:20px;margin-bottom:.5rem}.stepNumber.light{color:var(--on-surface)}.stepNumber.dark{color:var(--secondary2)}.stepTitle{font-size:20px;font-weight:700;line-height:28px}.stepTitle.light{color:var(--secondary)}.stepTitle.dark{color:var(--surface)}.stepDesc{font-size:16px;font-weight:400;line-height:24px}.stepDesc.light{color:var(--secondary)}.stepDesc.dark{color:var(--secondary2)}.divider{display:block;margin:0 auto;margin-bottom:0;padding-top:2rem}